// src/components/schedule/ScheduleNotesModal.tsx
'use client';

import React, { useState, useEffect } from 'react';
import { format, parse as parseDateFns, isValid as isValidDate } from 'date-fns';
import { es } from 'date-fns/locale';
import { Button } from '@/components/ui/button';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogClose,
} from '@/components/ui/dialog';
import { Calendar } from '@/components/ui/calendar';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Trash2, CalendarIcon, NotebookPen, PlusCircle, X, Pencil } from 'lucide-react'; // Added Pencil
import { cn } from '@/lib/utils';
import type { ScheduleNote, Employee } from '@/types/schedule';
import { useToast } from '@/hooks/use-toast';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"; // Import AlertDialog components

interface ScheduleNotesModalProps {
  isOpen: boolean;
  onClose: () => void;
  notes: ScheduleNote[];
  employees: Employee[];
  onAddNote: (newNoteData: Omit<ScheduleNote, 'id'>) => void;
  onDeleteNote: (noteId: string) => void;
  initialDate?: Date; // Optional initial date for context
}

export const ScheduleNotesModal: React.FC<ScheduleNotesModalProps> = ({
  isOpen,
  onClose,
  notes,
  employees,
  onAddNote,
  onDeleteNote,
  initialDate, // Receive initialDate
}) => {
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(initialDate || new Date());
  const [noteText, setNoteText] = useState('');
  const [selectedEmployeeId, setSelectedEmployeeId] = useState<string | undefined>(undefined);
  const [editingNoteId, setEditingNoteId] = useState<string | null>(null); // State for editing
  // Removed state for single delete confirmation, handled per item now
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();

  // Reset form when modal opens or initialDate changes
  useEffect(() => {
    if (isOpen) {
      setSelectedDate(initialDate || new Date());
      setNoteText('');
      setSelectedEmployeeId(undefined);
      setEditingNoteId(null); // Reset editing state
      setError(null);
    }
  }, [isOpen, initialDate]);

  const handleAddOrUpdateClick = () => {
    setError(null);
    if (!selectedDate) {
      setError('Por favor selecciona una fecha.');
      return;
    }
    if (!noteText.trim()) {
      setError('Por favor ingresa una nota.');
      return;
    }

    const noteData: Omit<ScheduleNote, 'id'> = {
      date: format(selectedDate, 'yyyy-MM-dd'),
      note: noteText.trim(),
      employeeId: selectedEmployeeId || undefined,
    };

    if (editingNoteId) {
      // Logic to update existing note (assuming onUpdateNote exists or is implemented)
      // Find the note and update it in the main notes array via a callback or directly if possible
      // For now, simulate update and show toast
      console.warn("Update note logic needs full implementation in parent component.");
      toast({ title: "Nota Actualizada (Simulado)", description: `Nota para ${format(selectedDate, 'PPP', { locale: es })} actualizada.` });
      // Find the index and update the note in the parent component's state
      // Example: updateNoteInParent(editingNoteId, noteData);
    } else {
      onAddNote(noteData); // Add new note
    }

    // Reset form fields after adding/updating
    setNoteText('');
    setSelectedEmployeeId(undefined);
    setEditingNoteId(null); // Exit editing mode
  };

  // Function to start editing a note
  const handleEditClick = (note: ScheduleNote) => {
    setEditingNoteId(note.id);
    const dateToSet = parseDateFns(note.date, 'yyyy-MM-dd', new Date());
    setSelectedDate(isValidDate(dateToSet) ? dateToSet : new Date()); // Fallback to today if parsing fails
    setNoteText(note.note);
    setSelectedEmployeeId(note.employeeId || undefined);
    setError(null);
  };

  // Note: confirmDeleteNote and handleDeleteConfirmed are removed as deletion is now handled within each item's AlertDialog

  return (
    <Dialog open={isOpen} onOpenChange={(open) => !open && onClose()}>
      <DialogContent className="sm:max-w-lg max-h-[90vh] flex flex-col">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <NotebookPen className="h-5 w-5" />
            Anotaciones Futuras / Eventos
          </DialogTitle>
          <DialogDescription>
            Agrega, edita o elimina notas para fechas especficas. Se mostrar un indicador (<span className="text-yellow-500"></span>) en el planificador esos das.
          </DialogDescription>
        </DialogHeader>

        {/* --- Formulario para aadir/editar nota --- */}
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 py-4 border-b pb-6">
          {/* Date Picker */}
          <div className="space-y-2 sm:col-span-1">
            <Label htmlFor="note-date">Fecha</Label>
            <Popover>
              <PopoverTrigger asChild>
                <Button
                  id="note-date"
                  variant={'outline'}
                  className={cn(
                    'w-full justify-start text-left font-normal overflow-hidden whitespace-nowrap text-ellipsis', // Added overflow styles
                    !selectedDate && 'text-muted-foreground'
                  )}
                  disabled={!!editingNoteId} // Disable date change when editing
                >
                  <CalendarIcon className="mr-2 h-4 w-4 flex-shrink-0" /> {/* Prevent shrink */}
                  {selectedDate ? format(selectedDate, 'PPP', { locale: es }) : <span>Selecciona fecha</span>}
                </Button>
              </PopoverTrigger>
              <PopoverContent className="w-auto p-0">
                <Calendar
                  mode="single"
                  selected={selectedDate}
                  onSelect={setSelectedDate}
                  initialFocus
                  locale={es}
                />
              </PopoverContent>
            </Popover>
             {!!editingNoteId && <p className="text-xs text-muted-foreground">No se puede cambiar la fecha al editar.</p>}
          </div>

          {/* Note Text */}
          <div className="space-y-2 sm:col-span-2">
             <Label htmlFor="note-text">Nota / Descripcin</Label>
             <Textarea
                id="note-text"
                value={noteText}
                onChange={(e) => setNoteText(e.target.value)}
                placeholder="Ej: Cumpleaos Carlos P., Solo hasta medioda..."
                rows={3}
             />
          </div>

          {/* Optional Employee Link */}
          <div className="space-y-2 sm:col-span-3">
            <Label htmlFor="note-employee">Colaborador (Opcional)</Label>
            <Select value={selectedEmployeeId} onValueChange={(value) => setSelectedEmployeeId(value === 'ninguno' ? undefined : value)}>
              <SelectTrigger id="note-employee">
                <SelectValue placeholder="Vincular a colaborador..." />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="ninguno">Ninguno</SelectItem>
                {employees.map(emp => (
                  <SelectItem key={emp.id} value={emp.id}>{emp.name}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

           {/* Error Message */}
          {error && <p className="text-sm text-destructive sm:col-span-3">{error}</p>}

          {/* Add/Update Button */}
          <div className="sm:col-span-3 flex justify-end gap-2">
              {editingNoteId && (
                  <Button variant="outline" onClick={() => { setEditingNoteId(null); setNoteText(''); setSelectedEmployeeId(undefined); setError(null); }}>
                       <X className="mr-2 h-4 w-4" /> Cancelar Edicin
                  </Button>
              )}
             <Button onClick={handleAddOrUpdateClick}>
                 {editingNoteId ? <Pencil className="mr-2 h-4 w-4" /> : <PlusCircle className="mr-2 h-4 w-4" />}
                 {editingNoteId ? 'Actualizar Anotacin' : 'Aadir Anotacin'}
             </Button>
          </div>
        </div>

        {/* --- Lista de notas existentes --- */}
        <div className="flex-grow overflow-hidden py-4">
          <h4 className="mb-3 font-medium text-sm text-foreground">Anotaciones Guardadas</h4>
          {notes.length > 0 ? (
            <ScrollArea className="h-[35vh] pr-4">
              <ul className="space-y-2">
                {notes.map((note) => {
                   const employeeName = note.employeeId ? employees.find(e => e.id === note.employeeId)?.name : null;
                   const noteDate = parseDateFns(note.date, 'yyyy-MM-dd', new Date());
                   const formattedDate = isValidDate(noteDate) ? format(noteDate, 'EEE dd MMM', { locale: es }) : note.date;
                  return (
                    <li key={note.id} className={cn(
                       "flex items-start justify-between p-2 border rounded-md bg-background text-sm",
                       editingNoteId === note.id && "ring-2 ring-primary ring-offset-1 bg-primary/10" // Highlight if editing
                    )}>
                      <div className="flex-grow mr-2">
                         <span className="font-medium text-foreground">{note.note}</span>
                         <span className="block text-xs text-muted-foreground">
                            {formattedDate}
                            {employeeName ? ` - ${employeeName}` : ''}
                         </span>
                      </div>
                      <div className="flex flex-shrink-0 gap-1">
                          {/* Edit Button */}
                          <Button
                             variant="ghost"
                             size="icon"
                             className="h-7 w-7 text-muted-foreground hover:text-primary"
                             onClick={() => handleEditClick(note)}
                             title="Editar anotacin"
                             disabled={!!editingNoteId && editingNoteId !== note.id} // Disable other edits while one is active
                          >
                             <Pencil className="h-4 w-4" />
                          </Button>
                          {/* Delete Button Wrapped in AlertDialog */}
                           <AlertDialog>
                              <AlertDialogTrigger asChild>
                                  <Button
                                     variant="ghost"
                                     size="icon"
                                     className="h-7 w-7 text-destructive hover:bg-destructive/10"
                                     title="Eliminar anotacin"
                                     disabled={!!editingNoteId} // Disable delete while editing
                                  >
                                     <Trash2 className="h-4 w-4" />
                                  </Button>
                              </AlertDialogTrigger>
                              <AlertDialogContent>
                                  <AlertDialogHeader>
                                      <AlertDialogTitle>Eliminar Anotacin?</AlertDialogTitle>
                                      <AlertDialogDescription>
                                          Esta accin eliminar permanentemente la anotacin "{note.note}". No se puede deshacer.
                                      </AlertDialogDescription>
                                  </AlertDialogHeader>
                                  <AlertDialogFooter>
                                      <AlertDialogCancel>Cancelar</AlertDialogCancel>
                                      {/* Call onDeleteNote directly when action is confirmed */}
                                      <AlertDialogAction className="bg-destructive hover:bg-destructive/90" onClick={() => onDeleteNote(note.id)}>
                                          Eliminar
                                      </AlertDialogAction>
                                  </AlertDialogFooter>
                              </AlertDialogContent>
                           </AlertDialog>
                      </div>
                    </li>
                  );
                 })}
              </ul>
            </ScrollArea>
          ) : (
            <p className="text-sm text-muted-foreground italic text-center py-4">No hay anotaciones guardadas.</p>
          )}
        </div>

        <DialogFooter>
          <DialogClose asChild>
            <Button variant="secondary">
              <X className="mr-2 h-4 w-4" /> Cerrar
            </Button>
          </DialogClose>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
};

    